<template>
  <el-container>
    <el-header height="200px">Header</el-header>
    <el-container>
      <el-aside width="300px" height="800px">
        <el-menu
          default-active="2"
          class="el-menu-vertical-demo"
          @select="selectMenu"
          @open="handleOpen"
          @close="handleClose"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b">
          <el-menu-item index="student">
            <span slot="title">管理学生信息</span>
          </el-menu-item>
          <el-menu-item index="banji">
            <span slot="title">管理班级信息</span>
          </el-menu-item>
          <el-menu-item index="updateUsername">
            <span slot="title">修改密码</span>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-main height="800px">
        <el-tabs v-model="selectedTab" type="card" editable  @edit="handleTabsEdit" @tab-remove="removeTab">
          <el-tab-pane
            :key="modules[key].name"
            v-for="key in tabs"
            :label="modules[key].title"
            :name="modules[key].name"
          >
            <component :is="modules[key].component"></component>
          </el-tab-pane>
        </el-tabs>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
    import stuShow from './stuShow'
    import banjiShow from './banjiShow'
    import updateUsernameShow from './updateUsernameShow'
    export default {
      data() {
        return {
          selectedTab: 'null',
          tabs:[],//元素和modules下元素的name相同
          modules:{
            student:{
              title:'学生管理',
              name:'student',
              component:stuShow
            },
            banji:{
              title:'班级管理',
              name:'banji',
              component:banjiShow
            },
            updateUsername:{
              title:'修改密码',
              name:'updateUsername',
              component:updateUsernameShow
            },
          },
        }
      },
      methods: {
        selectMenu(index){
          if(this.tabs.indexOf(index)<0){
            this.tabs.push(index);
          }
          this.selectedTab=index;
        },
        removeTab(name){
          let index=this.tabs.indexOf();
          this.tabs.splice(index,1);
          this.selectedTab = this.tabs[index-1];
        },
        handleOpen(key, keyPath) {
          console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
          console.log(key, keyPath);
        },
        handleTabsEdit(targetName, action) {
          if (action === 'add') {
            let newTabName = ++this.tabIndex + '';
            this.editableTabs.push({
              title: 'New Tab',
              name: newTabName,
              content: 'New Tab content'
            });
            this.editableTabsValue = newTabName;
          }
          if (action === 'remove') {
            let tabs = this.editableTabs;
            let activeName = this.editableTabsValue;
            if (activeName === targetName) {
              tabs.forEach((tab, index) => {
                if (tab.name === targetName) {
                  let nextTab = tabs[index + 1] || tabs[index - 1];
                  if (nextTab) {
                    activeName = nextTab.name;
                  }
                }
              });
            }
            this.editableTabsValue = activeName;
            this.editableTabs = tabs.filter(tab => tab.name !== targetName);
          }
        }
      }
    }
</script>

<style scoped>
  .el-header{
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
  }

  .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
  }

  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
  }
</style>



