<template>
  <div id="container">
    <el-table
      :data="students"
      border highlight-current-row
      @current-change="selectTr"
      style="width: 100%">
      <el-table-column
        prop="id"
        label="班号"
        width="250">
      </el-table-column>
      <el-table-column
        prop="name"
        label="班级"
        >
      </el-table-column>
    </el-table>
    <div style="margin-top:10px">
      <el-button type="primary" v-on:click="showAdd">添加</el-button>
      <el-button type="primary" v-on:click="showUpdate">修改</el-button>
      <el-button type="primary" v-on:click="deleteItem">删除</el-button>
    </div>
    <!--    新增操作对话框-->
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="50%"
    >
      <el-form ref="form" :model="form" label-width="80px" style="text-align: left">
        <el-form-item label="班级">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="add">确定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!--    修改操作对话框-->
    <el-dialog
      title="提示"
      :visible.sync="updatedialogVisible"
      width="50%"
    >
      <el-form ref="form" :model="form" label-width="80px" style="text-align: left">
        <el-form-item label="班级">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="update">确定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "banjiShow",
    data(){
      return{
        students:[{}],
        selectedId:-1,
        dialogVisible:false,
        updatedialogVisible:false,
        form:{
          id:this.selectedId,
          name:"",
        },
        bjs:[],
      }
    },
    methods:{
      search(){
        this.axios.get('http://localhost:8090/bj')
          .then(function (res) {
            //成功
            this.students = res.data;//此为后台响应回来的数据
          }.bind(this))
          .catch(function (err) {
            if (err.response){
              //失败
              console.log(err.response)
            }
          }.bind(this))
      },
      searchBjs(){
        this.axios.get('http://localhost:8090/bj')
          .then(function (res) {
            //成功
            this.bjs = res.data;//此为后台响应回来的数据
            //设置下拉框默认值
            this.form.bj.id=this.bjs[0].id
          }.bind(this))
          .catch(function (err) {
            if (err.response){
              //失败
              console.log(err.response)
            }
          }.bind(this))
      },
      searchById(){
        this.axios.get('http://localhost:8090/stu'+this.selectedId)
          .then(function (res) {
            //成功
            this.bjs = res.data;//此为后台响应回来的数据
            //设置下拉框默认值
            if(this.form.bj==null){
              this.form.bj.id=this.bjs[0].id
            }
          }.bind(this))
          .catch(function (err) {
            if (err.response){
              //失败
              console.log(err.response)
            }
          }.bind(this))
      },
      showAdd(){
        //this.$router.push({name:"StudentAdd", requiredAuth:true});
        this.searchBjs();
        this.dialogVisible = true;
      },
      showUpdate(){
        //this.$router.push({name:"StudentUpdate", params:{id:this.selectedId}, requiredAuth:true});
        this.searchBjs();
        this.searchById();
        this.updatedialogVisible = true;
      },
      add(){
        this.axios.post("http://localhost:8090/bj",  this.form)
          .then(function (res) {
            this.dialogVisible=false;
            this.search();
            this.form.name=""
            //成功
          }.bind(this))
          .catch(function (err) {
            if (err.response) {
              console.log(err.response)
            }
          }.bind(this))
      },
      selectTr(obj){
        this.selectedId = obj.id
      },
      deleteItem(){
        this.axios.delete('http://localhost:8090/bj/'+this.selectedId)
          .then(function (res) {
            //成功
            this.search();
          }.bind(this))
          .catch(function (err) {
            if (err.response){
              //失败
              console.log(err.response)
            }
          }.bind(this))
      },
      update(){
        this.form.id=this.selectedId
        this.axios.put("http://localhost:8090/bj",  this.form)
          .then(function (res) {
            this.updatedialogVisible=false;
            this.search();
            this.form.name=""
            //成功
          }.bind(this))
          .catch(function (err) {
            if (err.response) {
              console.log(err.response)
            }
          }.bind(this))
      }
    },
    created() {
      this.search();
    }
  }
</script>

<style scoped>
  #container {
    width: 510px;
    height: 800px;
    margin: 10px auto;
  }

  #data .selected{
    background: #50372c;
  }
</style>




